// Layout.js

import React from "react";
import "./Layout.css"; // CSS  import

// Header 
function Header() {
    return (
        <header className="header">
            <h1>My Page</h1>
            <nav>
                <a href="/github_myPage_react#section1">Home</a>
                <a href="/github_myPage_react#section2">About</a>
                <a href="/github_myPage_react#section3">Contact</a>
            </nav>
        </header>
    );
}

// Footer 
function Footer() {
    return (
        <footer className="footer">
            <p>&copy; {new Date().getFullYear()} My Page. All Rights Reserved.</p>
        </footer>
    );
}

// Layout 
function Layout({ children }) {
    return (
        <>
            <Header />
            <div id="fullpage">{children}</div>
            <Footer />
        </>
    );
}

export default Layout;
