// useMoonPhase.js

import { useEffect, useState } from "react";
import { fetchMoonPhase } from "../services/moonPhaseService";

const useMoonPhase = () => {
    const [moonPhase, setMoonPhase] = useState(""); //   
    const [emoji, setEmoji] = useState(""); //   

    useEffect(() => {
        const fetchPhaseData = async () => {
            const today = new Date().toISOString().split("T")[0]; //   yyyy-mm-dd 
            console.log(`  : ${today}`);

            try {
                const data = await fetchMoonPhase(today);
                console.log(" API  :", data);

                if (data && data.phase_description) {
                    const phaseName = data.phase_description; //   
                    setMoonPhase(phaseName);
                    console.log(`  : ${phaseName}`);
                    updateEmoji(phaseName); //  
                } else {
                    console.warn("     .");
                }
            } catch (error) {
                console.error(" API  :", error.message);
            }
        };

        const updateEmoji = (phase) => {
            const phases = {
                "New Moon": "",
                "Dark Moon": "",
                "Waxing Crescent": "",
                "First Quarter": "",
                "Waxing Gibbous": "",
                "Full Moon": "",
                "Waning Gibbous": "",
                "Last Quarter": "",
                "Waning Crescent": "",
                "Unknown Phase": "",
            };

            const selectedEmoji = phases[phase] || "";
            console.log(`  : ${selectedEmoji}`);
            setEmoji(selectedEmoji);
        };

        fetchPhaseData();
    }, []);

    return { moonPhase, emoji }; //    
};

export default useMoonPhase;
